.imageSection {
    padding: 30px;
  }
  #productImage {
    padding-left: 15px;
    padding-bottom: 15px;
  }
  .sizeButton {
    height: 45px;
    width: 45px;
    font-size: 13px;
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
    font-weight: bold;
    background-color: white;
    color: rgb(105, 100, 100);
    border: 1px solid rgb(201, 196, 196);
    border-radius: 50%;
  }
  .sizeButton:hover {
    border: 1px solid orange;
  }
  .sizeButton:focus {
    outline: none;
    border: 1px solid orange;
  }
  .sizeCross {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 45px;
    pointer-events: none;
    background-color: rgb(252, 248, 248);
    margin-right: 10px;
    border-radius: 50%;
    border-style: solid;
    border-width: thin;
    border-color: #bfbfbf;
  }
  .line {
    position: absolute;
    width: 100%;
    height: 1px;
    left: 5%;
    top: 70%;
    transform-origin: left center;
    background: rgb(209, 211, 209);
    transform: rotate(-30deg);
  }
  .addToCart {
    padding: 10px;
    border: none;
    width: 100%;
    background-color: orange;
    color: white;
    border-radius: 5px;
    font-weight: bold;
  }
  .addToCart:hover {
    background-color: rgb(238, 155, 2);
  }
  .addToCart:focus {
    outline: none;
  }
  .select {
    margin-left: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid rgb(185, 181, 181);
    border-radius: 5px;
  }
  .select:focus {
    outline: none;
  }
  .spectTitle {
    font-size: 12px;
    color: grey;
    margin-top: 5px;
  }
  .spectValue {
    margin-bottom: 5px;
  }
  .stock {
    font-size: 13px;
    padding-left: 15px;
    padding-top: 5px;
    color: orange;
  }
  @media only screen and (max-width: 500px) {
    .imageSection {
      margin-top: -44px;
      padding: 0 10px 10px 10px;
      margin-bottom: 10px;
    }
    .sizeButton {
      width: 45px;
      font-size: 13px;
      position: relative !important;
      display: inline-block;
      vertical-align: top;
      font-weight: bold;
      background-color: white;
      color: rgb(105, 100, 100);
      border: 1px solid rgb(201, 196, 196);
      border-radius: 50%;
    }
    .sizeCross {
      /* margin-right: 10px; */
      pointer-events: none;
      position: relative !important;
    }
    .addToCart {
      width: 95%;
    }
    .AddToCartButton {
      position: fixed;
      background-color: white;
      background: transparent;
      display: block;
      width: 100%;
      height: 60px;
      bottom: 0%;
      z-index: 1000;
    }
    .Specification {
      padding-bottom: 30px;
    }
  }
